___CSS_LOADER_EXPORT___.push([module.id, `:root {
	--light: hsl(0deg 0% 90%);
	--dark: hsl(0deg 0% 20%);
	--secondary: hsl(0deg 0% 50%);
	--glass: hsl(0deg 0% 100% / var(--glass-alpha));

	--main: var(--dark);

	--shadow: 2px 2px 5px 1px #00000030;
	--drop-shadow: 2px 2px 2px #00000030;
	--glass-alpha: 10%;

	--red: hsl(0deg 70% 40%);
	--red-lite: hsl(0deg 70% 80%);
}

body[data-theme="dark"] {
	--main: var(--light);
	--glass: hsl(0deg 0% 0% / var(--glass-alpha));
}

body[data-image="snowy-day"] {
	.credits {
		color: black;
	}
}

body[data-loading="true"] {
	cursor: wait;
	* {
		cursor: wait !important;
	}
}

body {
	width: 100vw;
	height: 100vh;
	background-size: cover;
	background-position: center;
  background-repeat: no-repeat;
	background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
	color: var(--main);
	font-family: "Inter", sans-serif;
	font-weight: 500;
	display: flex;
	align-items: center;
	justify-content: center;
	overflow: hidden;
}

h1, h2, h3, h4, h5, h6 {
	line-height: 1.15;
}

h1 {
	font-size: 4rem;
}

h2 {
	font-size: 2rem;
}

h3 {
	font-size: 1.5rem;
}

main {
	width: 80vw;
	height: 80vh;
	max-width: 800px;
	display: flex;
	flex-direction: column;
	gap: 1rem;
}

.current-data {
	display: flex;
	align-items: center;
	gap: 1rem;

	.column {
		gap: 0.5rem;
	}
}

.current-weather {
	overflow: hidden;
}

.current-condition {
	text-wrap: nowrap;
}

.weather-icon {
	flex-shrink: 0;
	width: 4rem;
	height: 4rem;
}

.current-datetime {
	margin-left: auto;
	align-items: flex-end;
}

.input-section {
	display: flex;
	align-items: center;
	width: 100%;
	gap: 2rem;
}

.search-input {
	flex: 1;
	padding: 1.5rem 2rem;
	border-radius: 9999px;
	border: none;
	box-shadow: var(--shadow);
	outline: none;
	&::placeholder {
		color: var(--secondary);
	}
}

.search-button {
	background: none;
	padding: 0;
	border: none;
	cursor: pointer;
	width: 3rem;
	height: 3rem;

	img {
		filter: drop-shadow(var(--drop-shadow));
	}
}

.forecast-data {
	display: flex;
	flex-direction: column;
	width: 100%;
	flex: 1;
	padding: 2rem;
	gap: 2rem;
	border-radius: 1rem;
	background: var(--glass);
	backdrop-filter: blur(1rem);
	box-shadow: var(--shadow);
}

nav {
	display: flex;
	justify-content: center;
	gap: 2rem;
}

button {
	color: inherit;
}

.hours-button, .days-button {
	background: none;
	border: none;
	cursor: pointer;
	border-bottom: 2px solid transparent;
	padding: 0;

	&.selected {
		border-color: var(--main);
	}
}

.credits {
	position: absolute;
	text-align: center;
	bottom: 0;
	left: 50%;
	transform: translateX(-50%);
	color: white;
	background: var(--glass);
	font-weight: 400;
	backdrop-filter: blur(1rem);
	padding: 0 0.25rem;
	border-radius: 9999px;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAAA;CACC,yBAAyB;CACzB,wBAAwB;CACxB,6BAA6B;CAC7B,+CAA+C;;CAE/C,mBAAmB;;CAEnB,mCAAmC;CACnC,oCAAoC;CACpC,kBAAkB;;CAElB,wBAAwB;CACxB,6BAA6B;AAC9B;;AAEA;CACC,oBAAoB;CACpB,6CAA6C;AAC9C;;AAEA;CACC;EACC,YAAY;CACb;AACD;;AAEA;CACC,YAAY;CACZ;EACC,uBAAuB;CACxB;AACD;;AAEA;CACC,YAAY;CACZ,aAAa;CACb,sBAAsB;CACtB,2BAA2B;EAC1B,4BAA4B;CAC7B,yDAA8C;CAC9C,kBAAkB;CAClB,gCAAgC;CAChC,gBAAgB;CAChB,aAAa;CACb,mBAAmB;CACnB,uBAAuB;CACvB,gBAAgB;AACjB;;AAEA;CACC,iBAAiB;AAClB;;AAEA;CACC,eAAe;AAChB;;AAEA;CACC,eAAe;AAChB;;AAEA;CACC,iBAAiB;AAClB;;AAEA;CACC,WAAW;CACX,YAAY;CACZ,gBAAgB;CAChB,aAAa;CACb,sBAAsB;CACtB,SAAS;AACV;;AAEA;CACC,aAAa;CACb,mBAAmB;CACnB,SAAS;;CAET;EACC,WAAW;CACZ;AACD;;AAEA;CACC,gBAAgB;AACjB;;AAEA;CACC,iBAAiB;AAClB;;AAEA;CACC,cAAc;CACd,WAAW;CACX,YAAY;AACb;;AAEA;CACC,iBAAiB;CACjB,qBAAqB;AACtB;;AAEA;CACC,aAAa;CACb,mBAAmB;CACnB,WAAW;CACX,SAAS;AACV;;AAEA;CACC,OAAO;CACP,oBAAoB;CACpB,qBAAqB;CACrB,YAAY;CACZ,yBAAyB;CACzB,aAAa;CACb;EACC,uBAAuB;CACxB;AACD;;AAEA;CACC,gBAAgB;CAChB,UAAU;CACV,YAAY;CACZ,eAAe;CACf,WAAW;CACX,YAAY;;CAEZ;EACC,uCAAuC;CACxC;AACD;;AAEA;CACC,aAAa;CACb,sBAAsB;CACtB,WAAW;CACX,OAAO;CACP,aAAa;CACb,SAAS;CACT,mBAAmB;CACnB,wBAAwB;CACxB,2BAA2B;CAC3B,yBAAyB;AAC1B;;AAEA;CACC,aAAa;CACb,uBAAuB;CACvB,SAAS;AACV;;AAEA;CACC,cAAc;AACf;;AAEA;CACC,gBAAgB;CAChB,YAAY;CACZ,eAAe;CACf,oCAAoC;CACpC,UAAU;;CAEV;EACC,yBAAyB;CAC1B;AACD;;AAEA;CACC,kBAAkB;CAClB,kBAAkB;CAClB,SAAS;CACT,SAAS;CACT,2BAA2B;CAC3B,YAAY;CACZ,wBAAwB;CACxB,gBAAgB;CAChB,2BAA2B;CAC3B,kBAAkB;CAClB,qBAAqB;AACtB","sourcesContent":[":root {\n\t--light: hsl(0deg 0% 90%);\n\t--dark: hsl(0deg 0% 20%);\n\t--secondary: hsl(0deg 0% 50%);\n\t--glass: hsl(0deg 0% 100% / var(--glass-alpha));\n\n\t--main: var(--dark);\n\n\t--shadow: 2px 2px 5px 1px #00000030;\n\t--drop-shadow: 2px 2px 2px #00000030;\n\t--glass-alpha: 10%;\n\n\t--red: hsl(0deg 70% 40%);\n\t--red-lite: hsl(0deg 70% 80%);\n}\n\nbody[data-theme=\"dark\"] {\n\t--main: var(--light);\n\t--glass: hs