	z-index: 1000;
	display: flex;
	flex-direction: column;
	gap: 0.5rem;
	position: absolute;
	top: 1rem;
	left: 50%;
	transform: translateX(-50%);
	width: 600px;
}

.error-message {
	display: flex;
	gap: 1rem;
	padding: 1rem 2rem;
	background: var(--red-lite);
	color: var(--red);
	align-items: center;
	font-weight: 700;
	border-radius: 9999px;
	border: 0.25rem solid var(--red);
	width: 100%;
}

.close-error-message {
	flex-shrink: 0;
	margin-left: auto;
	border: none;
	padding: 0;
	background: none;
	cursor: pointer;
	opacity: 0.5;

	&:hover {
		opacity: 1;
	}
}`, "",{"version":3,"sources":["webpack://./src/components/error/index.css"],"names":[],"mappings":"AAAA;CACC,aAAa;CACb,aAAa;CACb,sBAAsB;CACtB,WAAW;CACX,kBAAkB;CAClB,SAAS;CACT,SAAS;CACT,2BAA2B;CAC3B,YAAY;AACb;;AAEA;CACC,aAAa;CACb,SAAS;CACT,kBAAkB;CAClB,2BAA2B;CAC3B,iBAAiB;CACjB,mBAAmB;CACnB,gBAAgB;CAChB,qBAAqB;CACrB,gCAAgC;CAChC,WAAW;AACZ;;AAEA;CACC,cAAc;CACd,iBAAiB;CACjB,YAAY;CACZ,UAAU;CACV,gBAAgB;CAChB,eAAe;CACf,YAAY;;CAEZ;EACC,UAAU;CACX;AACD","sourcesContent":[".error-queue {\n\tz-index: 1000;\n\tdisplay: flex;\n\tflex-direction: column;\n\tgap: 0.5rem;\n\tposition: absolute;\n\ttop: 1rem;\n\tleft: 50%;\n\ttransform: translateX(-50%);\n\twidth: 600px;\n}\n\n.error-message {\n\tdisplay: flex;\n\tgap: 1rem;\n\tpadding: 1rem 2rem;\n\tbackground: var(--red-lite);\n\tcolor: var(--red);\n\talign-items: center;\n\tfont-weight: 700;\n\tborder-radius: 9999px;\n\tborder: 0.25rem solid var(--red);\n\twidth: 100%;\n}\n\n.close-error-message {\n\tflex-shrink: 0;\n\tmargin-left: auto;\n\tborder: none;\n\tpadding: 0;\n\tbackground: none;\n\tcursor: pointer;\n\topacity: 0.5;\n\n\t&:hover {\n\t\topacity: 1;\n\t}\n}"],"sourceRoot":""}]);
